.ztree * {
    padding: 0;
    margin: 0;
    font-size: 13px;
    font-family: Verdana, Arial, Helvetica, AppleGothic, sans-serif;
    background-color: transparent;
}

.ztree {
    margin: 0;
    padding: 5px 0;
    color: #666;
    background-color: transparent;
}

.ztree li {
    padding: 0;
    margin: 0;
    list-style: none;
    line-height: 30px;
    text-align: left;
    white-space: nowrap;
    outline: 0;
    position: relative;
    padding-top: 10px;
    padding-left: 5px;
}

.ztree li::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 0;
    top: 39px;
    left: 0;
    z-index: 1;
    border-bottom: 1px solid #ebebeb;
    background-color: transparent;
    border-radius: 3px;
}
.ztree li li::before {
    border-bottom: 1px dashed #ebebeb;
}

.ztree li ul {
    margin: 0px;
    padding: 0 0 0 18px;
}

.ztree li a {
    padding-right: 3px;
    margin: 0;
    cursor: pointer;
    height: 17px;
    color: #666;
    background-color: transparent;
    text-decoration: none;
    vertical-align: top;
    display: inline-block;
}

.ztree li a input.rename {
    height: 14px;
    width: 80px;
    padding: 0;
    margin: 0;
    color: #af0000;
    background-color: #666;
    font-size: 12px;
    border: 1px #585956 solid;
    *border: 0px;
}

.ztree li a:hover {
    text-decoration: underline;
}

.ztree li a.curSelectedNode {
    padding-top: 0px;
    background-color: transparent;
    color: #333;
    height: 17px;
    opacity: 0.8;
}

.ztree li a.curSelectedNode_Edit {
    padding-top: 0px;
    background-color: transparent;
    color: #333;
    height: 17px;
    border: 1px #666 solid;
    opacity: 0.8;
}

.ztree li a.tmpTargetNode_inner {
    padding-top: 0px;
    background-color: #eee;
    color: #333;
    height: 17px;
    border: 1px #666 solid;
    opacity: 0.8;
    filter: alpha(opacity=80);
}

.ztree li span {
    line-height: 17px;
    margin-right: 2px;
    background-color: transparent;
    display: inline-block;
    vertical-align: top;
    color: #25303f;
}

.ztree li span.button {
    line-height: 0;
    margin: 0;
    padding: 0;
    width: 15px;
    height: 17px;
    display: inline-block;
    vertical-align: top;
    border: 0px solid;
    cursor: pointer;
    outline: none;
    background-color: transparent;
    background-repeat: no-repeat;
    background-attachment: scroll;
}

.ztree li span.button::before {
    color: #858585;
    font-family: FontAwesome;
    padding-top: 10px;
    font-size: 12px;
}

.ztree li span.button.chk {
    margin: 0px;
    cursor: auto;
    width: 12px;
    display: inline-block;
    padding-top: 8px;
    padding-left: 2px;
}

